:root {
  --header-color: #000000;
  --nav-a-color: #ffffff;
  --nav-a-bg-mobile-devices-color: #563517;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  padding: 0;
  margin: 0;
}

/* header and nav styles */

.header {
  background-color: var(--header-color);
}

header {
  max-width: 1200px;
  padding: 1rem;
  margin: 0 auto;
}

#header-logo {
  max-width: 314px;
  max-height: 32px;
}

/* navigation styling across all the breakpoints */

nav ul {
  display: flex;
  gap: 1em;
  flex-flow: row wrap;
  justify-content: flex-end;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  /* I have selected 48% for flex basis because I want to have two li elements per line. */
  flex-basis: 48%;
  margin-bottom: 0.6rem;
}

nav li:first-child {
  /* in order for logo to be on his own line on mobile devices we set a flex-basis selector to 100% */
  flex-basis: 100%;
  text-align: center;
}

nav a {
  display: block;
  padding: 1rem;
  border-radius: 5px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.2rem;
  color: var(--nav-a-color);
  background-color: var(--nav-a-bg-mobile-devices-color);
  text-align: center;
}

nav li:first-child a {
  background-color: transparent;
}

@media (min-width: 550px) {
  nav ul {
    justify-content: center;
    gap: 0.5em;
  }
  nav li {
    flex-basis: auto;
    margin-bottom: 0;
  }
  nav a {
    font-size: 1.2rem;
    color: var(--nav-a-color);
    background-color: transparent;
  }
  nav a:hover {
    background-color: transparent;
  }
}

/* for desktop */
@media (min-width: 800px) {
  /* navigation for logo in the center */
  nav ul {
    justify-content: flex-end;
    gap: none;
  }

  nav a {
    font-size: 1.5rem;
  }

  nav li:first-child {
    flex-basis: auto;
    margin: 0 auto;
    order: 2;
  }
  nav li:nth-child(2),
  nav li:nth-child(3) {
    order: 1;
  }
  nav li:nth-child(4),
  nav li:nth-child(5) {
    order: 3;
  }
}
